@use '../base/' as *;
@use '../utils/' as *;
@use 'sass:color';

header{
  background-color: transparent;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .navContainer{
    width: 100vw;
    height: 100vh;
    background-color: color.scale($color-almost-black, $alpha: -30%);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    display: flex;
    justify-content: end;
    @include breakpoints_up (desktopBreakpoint){
      // Include styles
    }
    nav{
      padding: 1rem;
      background-color: $color-almost-white;
      width: min(65%, 300px);
      color: $color-medium-gray;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      // transform: translateX(100%);
      >button{
        width: fit-content;
        align-self: flex-end;
      }
      .list{
        display: flex;
        flex-direction: column;
        li{
          display: flex;
          align-items: center;
          gap: .6rem;
          i{
            transition: transform 300ms linear;
          }
        }
        a{
          width: 100%;
        }
        .sub-list a{
          padding-left: 10%;
          gap: 1rem;
          display: flex;
          align-items: center;
        }
      }
      .containerButtons{
        display: flex;
        gap: .5rem;
        flex-direction: column-reverse;
      }
    }
  }
}