@use '../base/' as *;

.outerContainer{
  display: grid;
  grid-column: 1fr;
  row-gap: .5rem;
  .leftColumnContainer{
    display: grid;
    grid-column: 1fr;
    padding: 1rem;
    text-align: center;
    figure{
      display: grid;
      grid-column: repeat(4,1fr);
      grid-auto-flow: column;
      align-items: center;
      gap: 1.6rem;
    }
    main{
      margin-bottom: 2rem;
      button{
        margin: 1rem 0;
        padding-inline: 1.4rem;
        background-color: $color-almost-black;
        color: $color-almost-white;
        border-radius: 1rem;
      }
    }
  }
}