@use 'sass:map';
@use '../base/' as *;
@use '../utils/' as *;


footer{
  font-size: $fs-footer;
  display: flex;
  flex-direction: column;
  align-items: center;
  a{
    color: $color-footer-a !important;
  }
  @include breakpoints_up (footerBreakpoint){
    flex-direction: row;
    justify-content: center;
    column-gap: .3rem;
  }
}